	module.exports = {"data":{"site":{"siteMetadata":{"title":"Psyked.co.uk","author":"James Ford"}},"markdownRemark":{"id":"/Users/James/Repositories/portfolio-2018/src/posts/link-icon-generator-pro/index.md absPath of file >>> MarkdownRemark","html":"<p><a href=\"http://icon-generator.net/\"><img src=\"http://uploads.psyked.co.uk/2010/05/icon-generator-pro.png\" title=\"Icon Generator Pro\"></a></p>\n<p>I love this application, Icon Generator Pro.  It's simple, but sometimes that's a good thing in an application, means it has focus - if you're going to do something, do it properly - that's one of the benefits of AIR, it makes it simple to create applications just like this.</p>\n<p>What the application actually does is just generate images in a few specific formats, output the files (in a format useful for your own AIR applications) and even, from the looks of it, replace your Twitter icon with your new file.</p>\n<p>If you haven't seen the earlier version of this application, its still available from the AIR Marketplace <a href=\"http://www.adobe.com/cfusion/marketplace/index.cfm?event=marketplace.offering&#x26;offeringid=10313&#x26;marketplaceid=1\">[link]</a>, and the newer version, Icon Generator Pro is available from <a href=\"http://icon-generator.net/\">http://icon-generator.net/</a>.</p>","frontmatter":{"title":"Link: Icon Generator Pro","date":"May 12, 2010","tags":["air-marketplace","icons"]}}},"pathContext":{}}

